import { Component, OnInit } from '@angular/core';
import { ContactUsService } from '../../services/contact-us.service';
import { FormBuilder, AbstractControl, FormGroup, FormControl, Validators } from '@angular/forms';
declare var $: any;
@Component({
    selector: 'app-contact-us-form',
    templateUrl: './contact-us-form.component.html',
    styleUrls: ['./contact-us-form.component.scss']
})
export class ContactUsFormComponent implements OnInit {
    NewMail: object;
    submitted = false;
    SendMailForm: FormGroup;
    constructor(private sendMail: ContactUsService, private fb: FormBuilder) { }

    ngOnInit(): void {
        this.SendMailForm = this.fb.group({
            FullName: ['', Validators.required],
            Subject: ['', Validators.required],
            Message: ['', Validators.required],
            Email: ['', [Validators.required, Validators.email]],

        });
    }
    get f() { return this.SendMailForm.controls; }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        if (this.SendMailForm.invalid) {
            return;
        }

        // display form values on success
        //alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.SendMailForm.value, null, 4));
        let FormMail = JSON.stringify(this.SendMailForm.value);
        this.sendMail.SetNewMail(FormMail);
    }

   
}
 